.navBar {
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    font-family: 'Bebas Neue', cursive;

    height: 55px;

    font-size: 45px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.22;
    letter-spacing: normal;
    text-align: left;

    color: #ECEDED;
    background-color: rgba(100, 100, 100, 0.63);
    z-index: 1;
}

